table {
  color: #383f4a;
}

th.background-color-grey {
  background-color: #f7f7f7;
}

th.background-color-white {
  background-color: #fff;
}

th.background-color-orange {
  background-color: #f80;
}

.button-container.for-table-pricing {
  position: relative;
  top: -2px;
}

.table-pricing_mobile thead th:first-child {
  border-radius: 0;
}

.table-pricing_mobile tbody tr:last-child td {
  left: 2px;
  position: relative;
  right: 2px;
  z-index: 2;
}

.table-pricing_mobile tbody tr:last-child th {
  left: -2px;
  z-index: 3;
}

@media screen and (min-width: 992px) {
  .table-pricing_mobile {
    display: none;
  }
}
@media screen and (max-width: 991px) {
  .table-pricing_mobile {
    border: 1px solid #fff;
    border-collapse: separate;
    border-spacing: 0;
    margin: 0;
    table-layout: fixed;
    white-space: nowrap;
  }
  .table-pricing_mobile tr.background-color-grey {
    background-color: #f7f7f7;
  }
  .table-pricing_mobile tbody tr:nth-child(13) td {
    font-size: 0.875rem;
    padding-top: 1rem;
  }
  .table-pricing_mobile td,
  .table-pricing_mobile th {
    border: 1px solid #fff;
    padding: 0.5rem 1rem;
  }
  .table-pricing_mobile thead th {
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    padding: 1rem;
    position: sticky;
    top: 0;
    width: 25vw;
    min-width: 220px;
    z-index: 1;
  }
  .table-pricing_mobile thead th div {
    white-space: unset;
  }
  .table-pricing_mobile td {
    padding: 4px 5px;
    text-align: center;
  }
  .table-pricing_mobile tbody th {
    font-weight: 100;
    position: relative;
    text-align: left;
    width: 245px;
  }
  .table-pricing_mobile thead th:first-child {
    left: 0;
    position: sticky;
    z-index: 2;
  }
  .table-pricing_mobile tbody th {
    left: 0;
    position: sticky;
    z-index: 1;
  }
  .table-pricing_mobile .table-pricing_column-border {
    border-radius: 0.5rem;
    bottom: 0;
    box-shadow: 0 0 14px -3px rgba(255, 136, 0, 0.5);
    height: 962px;
    left: 0;
    outline: 2px solid #f80;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
  }
  .table-pricing_mobile .table-pricing_column-shadow {
    bottom: -2px;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
    display: none;
    height: 962px;
    left: -2px;
    position: absolute;
    right: 0;
    top: -2px;
    transition: all 0.25s ease;
    z-index: 1;
  }
  [role=table][aria-labelledby][tabindex] {
    height: 968px;
    outline: none;
    overflow-x: auto;
    overflow-y: hidden;
    width: 100%;
  }
  [role=table][aria-labelledby][tabindex]:active .table-pricing_mobile .table-pricing_column-shadow,
  [role=table][aria-labelledby][tabindex]:focus .table-pricing_mobile .table-pricing_column-shadow,
  [role=table][aria-labelledby][tabindex]:hover .table-pricing_mobile .table-pricing_column-shadow {
    display: block;
  }
}
@media screen and (max-width: 479px) {
  table {
    font-size: 0.875rem;
  }
  [role=table][aria-labelledby][tabindex] {
    height: 470px;
    overflow-y: auto;
  }
  .table-pricing_mobile thead th {
    min-width: 180px;
  }
  .table-pricing_mobile .table-pricing_column-border {
    height: 466px;
  }
  .table-pricing_mobile tbody tr:last-child td {
    border-left: 2px solid transparent;
    border-right: 2px solid transparent;
    left: 0;
    right: 0;
  }
  .table-pricing_mobile tbody tr:nth-child(13) td {
    font-size: 0.65rem;
    padding-top: 1rem;
  }
  .line-break {
    display: block;
  }
}